body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #333;
  background-color: #ecf0f1;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

label {
  line-height: 2;
}

input {
  padding: .5rem;
  background-color: #fff;
  border: 1px solid #bdc3c7;
}

input:focus {
  border-color: #2980b9;
}

button {
  padding: .25rem 1rem;
  color: #fff;
  background-color: #2980b9;
  border: 1px solid #2980b9;
}

button:hover {
  background-color: #236d9e;
  border-color: #236d9e;
}

.app-header {
  padding-top: 2rem;
  padding-bottom: 2rem;
  color: #fff;
  background-color: #2c3e50;
}

.app-main {
  padding-top: 1rem;
  padding-bottom: 2rem;
}

.container {
  max-width: 740px;
  margin-right: auto;
  margin-left: auto;
}

.title {
  margin: 0 0 .75rem 0;
  font-size: 1.1rem;
}

.form-group {
  display: flex;
}

.form-group label {
  margin-right: .5rem;
}

.form-group input {
  margin-right: .5rem;
}

.form-group input::-webkit-input-placeholder {
  color: #666;
  font-style: italic;
}

.form-group input::-ms-input-placeholder {
  color: #666;
  font-style: italic;
}

.form-group input::placeholder {
  color: #666;
  font-style: italic;
}

.result {
  position: relative;
  padding: 2rem;
  border: 1px solid #bdc3c7;
  background-color: #fff;
}

.close {
  position: absolute;
  top: .5rem;
  right: .5rem;
  font-size: 0;
  color: #333;
  background: none;
  border: none;
}

.close:hover {
  color: #000;
  background: none;
  border: none;
}

.close:before {
  font-size: 1rem;
  content: "X";
}

.address {
  margin-bottom: 1rem;
  font-size: .9rem;
  font-style: normal;
}

.address span {
  font-size: 1.4rem;
  font-weight: bold;
  line-height: 1.8;
}

